<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="studentid">
				<el-input v-model="ruleForm.studentid" 
					placeholder="" clearable :disabled=" false  ||ro.studentid"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="gender">
				<el-select v-model="ruleForm.gender" placeholder="" :disabled=" false  ||ro.gender" >
					<el-option
						v-for="(item,index) in genderOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="age">
				<el-input v-model.number="ruleForm.age" 
					placeholder="" clearable :disabled=" false  ||ro.age"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="gradelevel">
				<el-input v-model="ruleForm.gradelevel" 
					placeholder="" clearable :disabled=" false  ||ro.gradelevel"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="professionalcategory">
				<el-input v-model="ruleForm.professionalcategory" 
					placeholder="" clearable :disabled=" false  ||ro.professionalcategory"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="scoreofmentalhealthscale">
				<el-input v-model.number="ruleForm.scoreofmentalhealthscale" 
					placeholder="" clearable :disabled=" false  ||ro.scoreofmentalhealthscale"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="SDS" prop="selfratingdepressionscalesdsscore">
				<el-input v-model.number="ruleForm.selfratingdepressionscalesdsscore" 
					placeholder="SDS" clearable :disabled=" false  ||ro.selfratingdepressionscalesdsscore"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="SAS" prop="selfratinganxietyscalesasscore">
				<el-input v-model.number="ruleForm.selfratinganxietyscalesasscore" 
					placeholder="SAS" clearable :disabled=" false  ||ro.selfratinganxietyscalesasscore"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="scoreonthestressscale">
				<el-input v-model.number="ruleForm.scoreonthestressscale" 
					placeholder="" clearable :disabled=" false  ||ro.scoreonthestressscale"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="emotionalstabilityscore">
				<el-input v-model.number="ruleForm.emotionalstabilityscore" 
					placeholder="" clearable :disabled=" false  ||ro.emotionalstabilityscore"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="GPA" prop="gpagpa">
				<el-input v-model.number="ruleForm.gpagpa" 
					placeholder="GPA" clearable :disabled=" false  ||ro.gpagpa"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="perceivedleveloflearningstress">
				<el-input v-model="ruleForm.perceivedleveloflearningstress" 
					placeholder="" clearable :disabled=" false  ||ro.perceivedleveloflearningstress"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="socialactivitylevel">
				<el-input v-model="ruleForm.socialactivitylevel" 
					placeholder="" clearable :disabled=" false  ||ro.socialactivitylevel"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="dailysleepduration">
				<el-input v-model="ruleForm.dailysleepduration" 
					placeholder="" clearable :disabled=" false  ||ro.dailysleepduration"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="regularityofdailyroutine">
				<el-input v-model="ruleForm.regularityofdailyroutine" 
					placeholder="" clearable :disabled=" false  ||ro.regularityofdailyroutine"></el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					studentid : false,
					gender : false,
					age : false,
					gradelevel : false,
					professionalcategory : false,
					scoreofmentalhealthscale : false,
					selfratingdepressionscalesdsscore : false,
					selfratinganxietyscalesasscore : false,
					scoreonthestressscale : false,
					emotionalstabilityscore : false,
					gpagpa : false,
					perceivedleveloflearningstress : false,
					socialactivitylevel : false,
					dailysleepduration : false,
					regularityofdailyroutine : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					studentid: '',
					gender: '',
					age: '',
					gradelevel: '',
					professionalcategory: '',
					scoreofmentalhealthscale: '',
					selfratingdepressionscalesdsscore: '',
					selfratinganxietyscalesasscore: '',
					scoreonthestressscale: '',
					emotionalstabilityscore: '',
					gpagpa: '',
					perceivedleveloflearningstress: '',
					socialactivitylevel: '',
					dailysleepduration: '',
					regularityofdailyroutine: '',
				},
				genderOptions: [],


				rules: {
					studentid: [
						{ required: true, message: '', trigger: 'blur' },
					],
					gender: [
					],
					age: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					gradelevel: [
					],
					professionalcategory: [
					],
					scoreofmentalhealthscale: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					selfratingdepressionscalesdsscore: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					selfratinganxietyscalesasscore: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					scoreonthestressscale: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					emotionalstabilityscore: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					gpagpa: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					perceivedleveloflearningstress: [
					],
					socialactivitylevel: [
					],
					dailysleepduration: [
					],
					regularityofdailyroutine: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='studentid'){
							this.ruleForm.studentid = obj[o];
							this.ro.studentid = true;
							continue;
						}
						if(o=='gender'){
							this.ruleForm.gender = obj[o];
							this.ro.gender = true;
							continue;
						}
						if(o=='age'){
							this.ruleForm.age = obj[o];
							this.ro.age = true;
							continue;
						}
						if(o=='gradelevel'){
							this.ruleForm.gradelevel = obj[o];
							this.ro.gradelevel = true;
							continue;
						}
						if(o=='professionalcategory'){
							this.ruleForm.professionalcategory = obj[o];
							this.ro.professionalcategory = true;
							continue;
						}
						if(o=='scoreofmentalhealthscale'){
							this.ruleForm.scoreofmentalhealthscale = obj[o];
							this.ro.scoreofmentalhealthscale = true;
							continue;
						}
						if(o=='selfratingdepressionscalesdsscore'){
							this.ruleForm.selfratingdepressionscalesdsscore = obj[o];
							this.ro.selfratingdepressionscalesdsscore = true;
							continue;
						}
						if(o=='selfratinganxietyscalesasscore'){
							this.ruleForm.selfratinganxietyscalesasscore = obj[o];
							this.ro.selfratinganxietyscalesasscore = true;
							continue;
						}
						if(o=='scoreonthestressscale'){
							this.ruleForm.scoreonthestressscale = obj[o];
							this.ro.scoreonthestressscale = true;
							continue;
						}
						if(o=='emotionalstabilityscore'){
							this.ruleForm.emotionalstabilityscore = obj[o];
							this.ro.emotionalstabilityscore = true;
							continue;
						}
						if(o=='gpagpa'){
							this.ruleForm.gpagpa = obj[o];
							this.ro.gpagpa = true;
							continue;
						}
						if(o=='perceivedleveloflearningstress'){
							this.ruleForm.perceivedleveloflearningstress = obj[o];
							this.ro.perceivedleveloflearningstress = true;
							continue;
						}
						if(o=='socialactivitylevel'){
							this.ruleForm.socialactivitylevel = obj[o];
							this.ro.socialactivitylevel = true;
							continue;
						}
						if(o=='dailysleepduration'){
							this.ruleForm.dailysleepduration = obj[o];
							this.ro.dailysleepduration = true;
							continue;
						}
						if(o=='regularityofdailyroutine'){
							this.ruleForm.regularityofdailyroutine = obj[o];
							this.ro.regularityofdailyroutine = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.genderOptions = ",".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`psychologicaldata/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`psychologicaldata/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
		}
	};
</script>

